import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Box from '../theme/Box/index';
import Text from '../theme/Text/index';
import Input from '../theme/Input/index';
import Button from '../theme/Button/index';
import Image from '../theme/Image/index';
import RegisterFormWrapper, { FormCol } from './registerForm.style';
import 'rc-tabs/assets/index.css';
import LogoImage from '../../images/logo.jpeg';
import LoginImage from '../../images/login-bg.jpg';
import useForm from '../../hooks/useForm';
import Select from '../theme/Select/index';
import firebase from 'gatsby-plugin-firebase';
import LeagueImage from '../LeagueImage/index';

var proxyUrl = 'https://cors-anywhere.herokuapp.com/';
const YAHOO_URL = `https://api.login.yahoo.com/oauth2/request_auth?response_type=code&redirect_uri=${process.env.GATSBY_WEBSITE_URL}&client_id=dj0yJmk9MkNLSmxtTUJMYWhjJmQ9WVdrOVpYUk1hWHBJTXpRbWNHbzlNQS0tJnM9Y29uc3VtZXJzZWNyZXQmc3Y9MCZ4PWVi`;
const YAHOO_KEY = 'yahoo_cache';

const options = [
    { value: 'espn', label: 'ESPN' },
    { value: 'sleeper', label: 'Sleeper' },
    { value: 'yahoo', label: 'Yahoo' },
];

function validateForm({ name, leagueid, email }) {
    if (name === '') {
        return "Name can't be empty";
    } else if (leagueid === '') {
        return "League id can't be empty";
    } else if (!/\d+$/.test(leagueid)) {
        return 'League id must only contain numbers';
    } else if (email === '') {
        return "Email can't be empty";
    }

    return null;
}

const RegisterForm = ({ row, col, btnStyle, logoStyle, contentWrapper, descriptionStyle }) => {
    const [values, setForm, manuallySetForm] = useForm({
        name: '',
        leagueid: '',
        email: '',
        platform: options[0],
    });
    const [message, setMessage] = useState({ content: 'Please fill out the data below' });
    const [isLoading, setLoading] = useState(false);
    const [hasloaded, setHasLoaded] = useState(false);

    useEffect(() => {
        async function fetchYahooData() {
            const queryString = window.location.search;
            const urlParams = new URLSearchParams(queryString);
            const yahoo_token = urlParams.get('code');
            if (!!yahoo_token) {
                setHasLoaded(true);
                setLoading(true);
                try {
                    let userData = JSON.parse(window.localStorage.getItem(YAHOO_KEY));
                    manuallySetForm(userData);
                    userData = { ...userData, platform: userData.platform.value };
                    const yahoo_access_token = await getUserToken(yahoo_token);
                    userData = { ...userData, yahoo_access_token };
                    await setUser(userData);
                } catch (err) {
                    setMessage({ content: err.message });
                } finally {
                    setLoading(false);
                }
            }
            setHasLoaded(true);
        }
        if (typeof window !== 'undefined') {
            fetchYahooData();
        }
    }, []);

    async function handleSubmit(e) {
        setLoading(true);
        e.preventDefault();
        let userData = { ...values, platform: values.platform.value };
        const error = validateForm(userData);
        if (error === null) {
            if (userData.platform === 'yahoo') {
                window.localStorage.setItem(YAHOO_KEY, JSON.stringify(values));
                const blankLink = document.createElement('a');
                blankLink.setAttribute('href', YAHOO_URL);
                // blankLink.setAttribute('target', '_blank');
                blankLink.style.display = 'none';
                document.body.appendChild(blankLink);
                blankLink.click();
                document.body.removeChild(blankLink);
            } else {
                await setUser(userData);
            }
        } else {
            setMessage({ content: error });
        }
        setLoading(false);
    }

    async function setUser(user) {
        const testDoc = firebase
            .firestore()
            .collection('users')
            .doc(`${user.platform}-${user.leagueid}`);
        const doc = await testDoc.get();
        if (!doc.exists && user.leagueid !== '' && user.name !== '') {
            firebase
                .firestore()
                .collection('users')
                .doc(`${user.platform}-${user.leagueid}`)
                .set(user);
            setMessage({
                content: `Thank you ${user.name}, your league at ${user.platform} with id ${user.leagueid} was successfully registered!`,
            });
        } else {
            setMessage({
                content: `League at ${user.platform} with id ${user.leagueid} already registered. If this is a mistake send an email to fantasy@squidsource.com`,
            });
        }
    }

    async function getUserToken(yahooToken) {
        if (typeof window !== 'undefined') {
            const request = await fetch(proxyUrl + 'https://api.login.yahoo.com/oauth2/get_token', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    Authorization:
                        'Basic ZGoweUptazlNa05MU214dFRVSk1ZV2hqSm1ROVdWZHJPVnBZVWsxaFdIQkpUWHBSYldOSGJ6bE5RUzB0Sm5NOVkyOXVjM1Z0WlhKelpXTnlaWFFtYzNZOU1DWjRQV1ZpOjUyZDcyYTg2NWUzY2M2YmQxNjhjN2Y2ODFjNzhiZjNkN2I3NWU2ZjQ=',
                },
                body: `grant_type=authorization_code&redirect_uri=oob&code=${yahooToken}`,
            });

            if (request.status === 200) {
                const { access_token } = await request.json();
                return access_token;
            } else {
                throw new Error('Invalid token');
            }
        }
    }

    return (
        <RegisterFormWrapper>
            <Box className="row" {...row}>
                <Box className="col imageCol h-100" {...col}>
                    <Image className="patternImage" src={LoginImage} alt="Login Banner" />
                </Box>
                <Box className="col tabCol" {...col}>
                    <Box {...contentWrapper}>
                        {hasloaded && (
                            <FormCol>
                                <Image src={LogoImage} {...logoStyle} alt="Logo" />
                                <br></br>
                                <Text content={`${message.content}`} {...descriptionStyle} />

                                <form onSubmit={handleSubmit}>
                                    <Input
                                        name="name"
                                        value={values.name}
                                        onChange={setForm}
                                        inputType="text"
                                        isMaterial
                                        label="Name"
                                    />
                                    <Input
                                        name="email"
                                        value={values.email}
                                        onChange={setForm}
                                        inputType="email"
                                        isMaterial
                                        label="Email"
                                    />
                                    <Select
                                        name="platform"
                                        labelText="Platform"
                                        options={options}
                                        isSearchable={false}
                                        name="platform"
                                        value={values.platform}
                                        onChange={(e) =>
                                            setForm({ target: { value: e, name: 'platform' } })
                                        }
                                    ></Select>
                                    <LeagueImage league={values.platform.value} />
                                    <Input
                                        name="leagueid"
                                        value={values.leagueid}
                                        onChange={setForm}
                                        inputType="text"
                                        isMaterial
                                        label="League Id (See image above)"
                                    />

                                    <div>
                                        <Button
                                            className="default"
                                            type="submit"
                                            title="Submit"
                                            isLoading={isLoading}
                                            {...btnStyle}
                                        />
                                    </div>
                                </form>
                            </FormCol>
                        )}
                    </Box>
                </Box>
            </Box>
        </RegisterFormWrapper>
    );
};

RegisterForm.propTypes = {
    row: PropTypes.object,
    col: PropTypes.object,
    logoStyle: PropTypes.object,
    titleStyle: PropTypes.object,
    hintTextStyle: PropTypes.object,
    contentWrapper: PropTypes.object,
    descriptionStyle: PropTypes.object,
    googleButtonStyle: PropTypes.object,
};

// LoginModal default style
RegisterForm.defaultProps = {
    // Team member row default style
    row: {
        flexBox: true,
        flexWrap: 'wrap',
    },
    // Team member col default style
    col: {
        width: [1, 1 / 2],
    },
    // Default logo size
    logoStyle: {
        width: '350px',
        height: 'auto',
        ml: '15px',
    },
    // Title default style
    titleStyle: {
        fontSize: ['22px', '36px', '50px'],
        fontWeight: '400',
        color: '#20201D',
        letterSpacing: '-0.025em',
        mt: '35px',
        mb: '10px',
    },
    // Description default style
    descriptionStyle: {
        color: 'red',
        fontSize: '16px',
        lineHeight: '26px',
        letterSpacing: '-0.025em',
        mb: '23px',
        ml: '1px',
    },
    // Content wrapper style
    contentWrapper: {
        pt: ['32px', '56px'],
        pl: ['17px', '32px', '38px', '40px', '56px'],
        pr: '32px',
        pb: ['32px', '56px'],
    },
    // Default button style
    btnStyle: {
        minWidth: '156px',
        fontSize: '14px',
        fontWeight: '500',
    },
    // Outline button outline style
    outlineBtnStyle: {
        minWidth: '156px',
        fontSize: '14px',
        fontWeight: '500',
        color: 'rgb(26, 115, 232)',
    },
    // Google button style
    googleButtonStyle: {
        bg: '#ffffff',
        color: '#343D48',
    },
};

export default RegisterForm;
